@charset "utf-8";

.todo_item{
  display: flex; justify-content: space-between;
  align-items: center;
  cursor: pointer;
  line-height: 50px;
  border-radius: 20px;
  box-shadow: var(--shadow);
  background: var(--white_opa_2);
  margin: 20px;
  padding: 10px 20px;
}
.todo_item:hover{
  background: var(--white_opa_1);
}
.todo_item:hover .remove{
  opacity: 1;
}
.remove{
  opacity: 0;
  font-size: 2rem;
  transition: 0.5s;
}
.todo_item:has(.todo_textchecked){
  background: var(--white_opa_1);
}
.todo_textchecked{
  color: var(--gray);
}
.todo_textchecked .check_mark{
  box-shadow: var(--inner-shadow);
}
.check_mark{
  background: var(--bg_color);
  border-radius: 50%;
  padding: 5px; margin-right: 5px;
  vertical-align: middle;
  box-shadow: var(--shadow);
  transition: 0.3s;
}