@charset "utf-8";

.form{
  width: calc(100% - 1rem);
  display: flex; gap: 1rem;
  justify-content: space-between;
  /* padding: 1rem; */
}
/* .form *{flex: 1;} */
.form input{
  flex: 1;
  font-size: 1.25rem;
  background: var(--bg_color);
  box-shadow: var(--shadow);
  padding: 1rem 0.5rem;
  box-sizing: border-box;
  border: none; border-radius: 1rem;
  outline: none;
  color: var(--font_color);
  text-indent: 0.5rem;
}
.form input:focus, .form input:focus-visible{
  box-shadow: var(--inner-shadow);
}
.create_btn{
  padding-inline: 1rem;
  padding-top: 0.3rem;
  font-size: 1.5rem;
  border: none;
  color: var(--bg_color);
  background: var(--black_opa_1);
  border-radius: 50%;
  box-sizing: border-box;
}
.create_btn:hover{
  background: var(--black_opa_2);
}