@charset "utf-8";
/* index.css */
@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+KR:wght@100;200;300;400;500;600;700&display=swap');

:root {
  --bg_color: #ffffff;
  --font_color: #333333;
  --gray: #ccc;
  background: url('https://cdn.pixabay.com/photo/2019/10/25/22/03/sky-4578239_1280.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%; height: max-content; min-height: 100vh;
  background-position: center;
  transition: 0.3s;
  --shadow: .15rem .15rem .3rem var(--gray), 
-.1rem -.1rem .25rem var(--bg_color);
  --inner-shadow: inset .1rem .1rem .25rem var(--gray), inset -.1rem -.1rem .25rem var(--bg_color);
  --white_opa_1: rgba(255,255,255,0.5);
  --white_opa_2: rgba(255,255,255,0.8);
  --black_opa_1: rgba(0,0,0,0.5);
  --black_opa_2: rgba(0,0,0,0.8);
}

[color_toggle=dark] {
  --bg_color: #333333;
  --font_color: #ffffff;
  --gray: #666;
  background-image: url('https://cdn.pixabay.com/photo/2022/02/18/18/31/moon-7021271_1280.jpg');
  --shadow: .15rem .15rem .3rem #000, 
-.1rem -.1rem .25rem var(--bg_color);
  --inner-shadow: inset .1rem .1rem .25rem #000, inset -.1rem -.1rem .25rem var(--bg_color);
  --black_opa_1: rgba(255,255,255,0.5);
  --black_opa_2: rgba(255,255,255,0.8);
  --white_opa_1: rgba(0,0,0,0.5);
  --white_opa_2: rgba(0,0,0,0.8);
}

html{
  margin: 0; padding: 0;
}

body{
  font-family: "IBM Plex Sans KR", sans-serif;
  margin: 0 auto;
  /* background: var(--bg_color); */
  color: var(--font_color);
  width: 100%; max-width: 1280px;
}

#color_toggle {
  position: relative;
  width: -moz-max-content;
  width: max-content;
  margin: 20px auto;
}
#color_toggle input {
  display: none;
}
#color_toggle label {
  position: relative;
  display: block;
  width: 120px;
  height: 60px;
  margin: 0 auto;
  background: var(--bg_color);
  color: var(--font_color);
  border-radius: 40px;
  transition: 0.5s;
  box-shadow: var(--shadow);
}
#color_toggle label::after {
  position: absolute;
  top: 8px;
  left: 11px;
  z-index: 2;
  content: "ON";
  /* color: var(--bg_color); */
  line-height: 44px;
  text-align: center;
  font-weight: 900;
  font-size: 14px;
  display: block;
  width: 44px; height: 44px;
  border-radius: 50%;
  /* background: var(--font_color); */
  box-shadow: var(--inner-shadow);
  transition: 0.5s;
}
#color_toggle label::before {
  position: absolute;
  top: 14px;
  right: 22px;
  z-index: 2;
  content: "Light";
  color: var(--font_color);
  line-height: 28px;
  text-align: center;
  font-size: 14px;
  display: block;
  width: 28px;
  height: 28px;
  transition: 0.5s;
}
#color_toggle #switch:checked + label {
  /* background: var(--font_color); */
}
#color_toggle #switch:checked + label::after {
  content: "OFF";
  color: var(--font_color);
  background: var(--bg_color);
  transform: rotate(360deg);
  left: 66px;
}
#color_toggle #switch:checked + label::before {
  content: "Dark";
  color: var(--font_color);
  left: 22px;
}