@charset "utf-8";

.todo_list_template{
  width: 94%;
  /* background: var(--bg_color); */
  margin: 4rem auto 0 auto;
  box-shadow: var(--shadow);
  border-radius: 20px;
}
.title{
  display: flex;
  justify-content: space-between;
  padding: 1rem; margin-bottom: 2rem;
  align-items: center;
  border-bottom: 1px solid var(--bg_color);
}
.title .date{
  color: #fff;
  font-size: 2rem;
  text-align: center;
  font-weight: bold;
}
.form_wrapper{
  padding-inline: 1rem;
}
.todos_wrapper{
  min-height: 5rem;
  padding: 1rem;
}